<template>
  <div class="Mv">
    <div class="music-container">

      <!-- 最新 MV -->
      <div class="tab">
        <span :class="['item',newType==index? 'active': '']" v-for="(item, index) in newTab" :key="index" @click="getNewMv(index)">{{item}}</span>
      </div>
      <div class="mv-content">
        <mv-item
          class="item"
          v-for="(item, index) in newmvList"
          :key="index"
          :mvId="item.id"
          :cover="item.cover"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
        /> 
      </div>

      <!-- 推荐 MV -->
      <div class="introduce-mv mv-header">
        <h3>推荐 MV</h3>
      </div>
      <div class="mv-content">
        <mv-item
          class="item"
          v-for="(item, index) in introducemvList"
          :key="index"
          :mvId="item.id"
          :cover="item.picUrl"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
        />
      </div>

      <!-- 热门 MV -->
       <div class="introduce-mv mv-header">
        <h3>热门 MV</h3>
      </div>
      <div class="mv-content">
        <mv-item
          class="item"
          v-for="(item, index) in topmvList"
          :key="index"
          :mvId="item.id"
          :cover="item.cover"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
        />
      </div>

    </div>
  </div>
</template>
<script>
// 组件包
import mvItem from "../components/item/mvItem";
// 工具包
import tools from "../tools/formatData.js";
import { req_new_mv, req_introduce_mv } from "../network/mv/index.js";
import { req_rank_mv } from "../network/mv/index.js";

export default {
  name: "Mv",
  components: {
    mvItem,
  },
  data() {
    return {
      // 最新 MV 类型（对应 new_tab 的数组下标）
      newType: 0,
      // 最新 MV 类型数组
      newTab: ["内地", "港台", "欧美", "日本", "韩国"],
      // 最新 MV 数组
      newmvList: [],
      // 最新 MV 显示数量
      newmvNum: 12,

      // 推荐 MV
      introducemvList: [],

      // 热门 MV
      topmvList: [],
      // 热门 MV 显示数量
      topNum: 28,
      // 分页器标记
      paginationShow: false,
    };
  },
  watch: {
    newmvList: function() {
      this.newmvList.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 1);
        arr[index].playCount = value;
      });
    },
    introducemvList: function() {
      this.introducemvList.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 1);
        arr[index].playCount = value;
      });
    },
    topmvList: function() {
      this.topmvList.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 1);
        arr[index].playCount = value;
      });
    },
  },
  created() {
    this.getNewMv(0);
    this.getIntroduceMv();
    this.getTopMv();
  },
  methods: {
    // 获取最新歌单
    getNewMv(index = this.type) {
      // 出现 data 中 的 type 使其能够触发样式改变
      this.newType = index;
      req_new_mv(this, {
         limit: this.newmvNum,
         area: this.newTab[this.newType],
      }).then(res => {
          this.newmvList = res.data;
      })
      .catch(error => {
         console.log("获取最新 MV 出现错误", error);
      });
    },
    // 获取推荐歌单
    getIntroduceMv() {
      req_introduce_mv(this)
        .then(res => {
          this.introducemvList = res.result;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 热门 mv
    getTopMv() {
      req_rank_mv(this, {
         limit: this.topNum,
         area: "内地"
      }).then(res => {
        this.topmvList = res.data;
      }).catch(error => {
        console.log(error);
      })
    },
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.Mv {
  .music-container {
    .tab {
      @include flex(flex-start);
      margin: 12px 0px;
      padding-bottom: 12px;
      border-bottom: 1px solid rgba($color: #000000, $alpha: 0.4);
      .item {
        display: block;
        padding: 10px, 12px;
        margin-right: 12px;
        text-align: center;
        @include font(16px, 20px, $colorB);
        &:hover {
          color: $colorA;
          cursor: pointer;
        }
      }
      .active {
        color: $colorA;
      }
    }
    .mv-content {
      @include flex();
      flex-wrap: wrap;
    }
    .mv-header {
      h3 {
        font-size: 20px;
        margin-top: 45px;
        margin-bottom: 20px;
      }
    }
  }
}
</style>