<template>
    <div class="mv-item" @click="goMvDetail(mvId)">
        <img v-lazy="cover" alt="">
        <p class="name" @click="goMvDetail(mvId)">{{name}} / {{artistName}}</p>
        <div class="artists">
            <span class="artist" v-for="(art, i) in artists" :key="i" @click.stop="goSingerDetail(art.id, art.name)">{{art.name}}</span>
        </div>
        <div class="mask">
                <div class="count">播放量 {{playCount}}</div>
        </div>
    </div>
</template>
<script>
export default {
  name: "playList-item",
  props: {
    // MV id
    mvId: Number,
    // 图片地址
    cover: String,
    // MV 名字
    name: String,
    // 歌手
    artistName: String,
    // 其他歌手
    artists: Array,
    // 播放量
    playCount: String,
  },
  watch: {
  },
  methods: {
    goMvDetail(id) {
       this.$router.push({
         path: `/mvdetail/${new Date().getTime()}`,
         query: {id},
       })
    },
    goSingerDetail(id, name) {
        this.$router.push({
          path: `/singerDetail/${new Date().getTime()}`,
          query: {
            id,
            name,
          }
        })
    }
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
.mv-item {
  position: relative;
  width: 280px;
  margin-bottom: 20px;
  background-color: $colorD;
  padding-bottom: 6px;
  box-sizing: border-box;
  z-index: 100;
  img {
    display: block;
    width: 100%;
    height: 140px;
  }
  .name {
    margin-top: 6px;
    @include font(16px, 20px, $colorC);
    padding-left: 3px;
    @include fontHidden();
    cursor: pointer;
    &:hover {
      color: $colorA;
    }
  }
  .artists {
    @include flex(flex-start);
    padding-left: 3px;
    margin-top: 12px;
    .artist {
      margin-right: 6px;
      cursor: pointer;
      @include fontHidden();
      &:hover {
        color: $colorA;
      }
    }
  }
  .mask {
    @include position(100%, 140px);
    @include setMask(45px, 45px);
    background-position-y: calc(54px);
    .count {
      height: 40px;
      box-sizing: border-box;
      padding-left: 12px;
      background-color: rgba(0, 0, 0, 0.3);
      @include font(14px, 40px, $colorD);
    }
  }
  &:hover {
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.3);
    transform: translate3d(0, -2px, 0);
    .mask {
      opacity: 1;
    }
  }
}
</style>